.wrapperMovie{
    width: 600px;
    margin: 0 auto;
}

.imgPoster {
    display: block;
    margin: 0 auto;
    width: 300px;
    height: 400px;
}



.linkCastRewievs {
    display: block;
    text-decoration: none;
    padding: 10px;
    border: 1px solid black;
    width: 120px;
   
    text-align: center;
    border-radius: 5px;
    color: black;
    transition: 250ms linear;
    background-color: burlywood;
}

.linkCastRewievs:hover {
    background-color: blueviolet;
    color: white;
}

 .wrapperLink {
    display: flex;
    justify-content: space-between;
} 

:global .MovieDetails_linkCastRewievs__ACbxb.active {
    background-color: black;
    color: white;
}

.spanMap {
    margin-left: 10px;
}

